.swiper-slide *{
    box-sizing: border-box;
}

@media(min-width: 992px){
    #main-slider {
        .swiper-slide{
            overflow: hidden;
        }
        .swiper-slide-active{
            .main-slider__item__content__wrap{
                animation: fadeInLeftBig 0.5s linear;
            }
        }
    }
}

.swiper__action{
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 1;
    width: 100%;
    .wrap{
        position: relative;
    }
    .swiper-button-next, .swiper-button-prev{
        right: 30px;
        left: auto;
        top: auto;
        bottom: 36px;
        height: 40px;
        width: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        transform: translate(0);
    }
    .swiper-button-next:after, .swiper-button-prev:after{
        font-size: 15px;
        color: #fff;
    }
    .swiper-button-prev{
        right: 94px;
    }
    .swiper-button-prev:before{
        content: "";
        display: block;
        position: absolute;
        right: -12px;
        height: 34px;
        border-right: 1px solid rgba($color: #fff, $alpha: 0.2);
    }
    .swiper-button-next:hover, .swiper-button-prev:hover{
        background-color: rgba($color: #fff, $alpha: 0.1);
    }
    .swiper-pagination{
        left: auto;
        right: 30px;
        bottom: 227px;
        display: flex;
        align-items: center;
        top: auto;
        width: auto;
        font-size: 14px;
        color: rgba($color: #fff, $alpha: 0.2);
        .swiper-pagination-current{
            color: #fff;
        }
        .line{
            display: block;
            width: 102px;
            border-bottom: 1px solid rgba($color: #fff, $alpha: 0.1);
            margin-right: 22px;
            margin-left: 22px;
        }
    }   
}
